@use 'base/variables' as v;
@use 'base/mixins' as m;

.no-webp .bg {
    background-image: url(../../build/img/bg-intro-mobile.png);

    @include m.tablet {
     background-image: url(../../build/img/bg-intro-desktop.png);   
    }
}

.webp .bg {
    background-image: url(../../build/img/bg-intro-mobile.webp);

    @include m.tablet {
     background-image: url(../../build/img/bg-intro-desktop.webp);   
    }
}

.bg {
    background-color: v.$rojo;
    background-size: cover;
}

.header {
    margin-bottom: 7rem;
    flex: 1;

    p {
        text-align: center;
    }

    @include m.tablet {
        margin: auto 0;

        h1 {
            text-align: left;
            max-width: 40rem;
            font-size: 4rem;
            line-height: 4.5rem;
        }

        p {
            text-align: left;
        }
    }
}

.plan {
    background-color: v.$azul;
    padding: 2rem 5rem;
    margin-bottom: 3rem;
    border-radius: 1rem;
    box-shadow: 0 8px 0 v.$azulOscuroTransparente;

    p {
        text-align: center;
    }

    span {
        font-weight: v.$bold;
    }
}