@use 'base/variables' as v;
@use 'base/mixins' as m;

h1, 
h2, 
h3 {
    text-align: center;
    font-weight: v.$bold;
    margin: 0 0 calc(v.$separacion / 3) 0;
    line-height: 3.5rem;
}

h1 {
    font-size: 2.6rem;
}

h2 {
    font-size: 2.3rem;
}

h3 {
    font-size: 2rem;
    text-align: center;
}

p {
    font-size: 1.6rem;
    line-height: 2.5rem;
    margin: 0;
}
