@use 'base/variables' as v;
@use 'base/mixins' as m;

.formulario_plan {
    flex: 1;
}

.formulario {
    background-color: v.$blanco;
    border-radius: 1rem;
    padding: 2rem;
    margin-bottom: v.$separacion;
    box-shadow: 0 8px 0 v.$azulOscuroTransparente;

    @include m.tablet {
        padding: 4rem;
        margin-bottom: 0;
    }

    .campo {
        display: flex;
        align-items: center;
        border: 1px solid v.$azulOscuroTransparente;
        margin-bottom: 1.5rem;
        border-radius: .5rem;
        padding: 0 1.5rem;

        &:focus {
            outline: none;
            border: 1px solid v.$azulOscuro;
        }

        img {
            visibility: hidden;
            opacity: 0;
            height: 0;
        }

        &.error {
            border: 2px solid v.$rojo;
            
            input:not([type="submit"]) {
                color: v.$rojo;

                &::placeholder {
                    color: v.$blanco;
                }
            }

            img {
                visibility: visible;
                opacity: 1;
                height: auto;

                transition-property: opacity height;
                transition-duration: 1000ms;
            }
        }

        &.focus {
            border: 2px solid v.$azulGrisaceo;
        }
    }
    

    input:not([type="submit"]) {
        width: 100%;
        border: none;
        font-weight: v.$semiBold;
        padding: 1.5rem 0;
        border-radius: 1rem;

        
        &::placeholder {
            font-weight: v.$semiBold;
        }

        &:focus {
            outline: none;
        }
        

    }
    
    fieldset {
        border: none;
        padding: 0;
    }

    
    input[type="submit"] {
        @include m.boton(v.$verde, v.$blanco);
        margin: 0;
        border-radius: .5rem;
        padding: 1.5rem 0;
        font-weight: v.$regular;
        text-transform: uppercase;
        box-shadow: inset 0px -4px 0px darken(v.$verde, 5%);
        font-size: 1.6rem;

        &:hover {
            background-color: lighten(v.$verde, 15%);
        }
    }

    p {
        padding: 0 1rem;
        color: v.$azulGrisaceo;
        margin-top: 1rem;
        font-size: 1.1rem;
        text-align: center;

        span {
            color: v.$rojo;
            font-weight: v.$semiBold;
        }
    }
}