@use 'base/variables' as v;
@use 'base/mixins' as m;

html {
    font-size: 62.5%;
    box-sizing: border-box;
    height: 100%;
}

*, 
*::after, 
*::before {
    box-sizing: inherit;
}

body {
    font-size: 1.6rem;
    font-family: v.$fuente_principal;
    color: v.$blanco;
    padding: 8rem 0 3rem 0;

    @include m.tablet {
        padding: 0;
        min-height: 100vh;

        display: flex;
        align-items: center;
        justify-content: center;
    }
}

.contenedor {
    width: 90%;
    max-width: 1100px;
    margin: 0 auto;

    @include m.tablet {
        display: flex;
        gap: v.$separacion;
        margin: 2rem;
    }
}

input {
    font-family: v.$fuente_principal;
}

a {
    text-decoration: none;
}

img {
    max-width: 100%;
    display: block;
}